import request from '@/utils/request'
// 发布商品
export function getPrePayConfig(data) {
  return request({
    url: `/order/recharge/getPrePayConfig?shopId=${data.shopId}&money=${data.money}&operateId=${data.operateId}`,
    method: 'get',
  })
}
// 查询店铺提现信息
export function getShopWithdrawAmount(id) {
  return request({
    url: `/order/mall/withdraw/getShopWithdrawAmount?shopId=${id}`,
    method: 'get',
  })
}
// 提现
export function shopApplyWithdraw(data) {
  return request({
    url: `/order/mall/withdraw/shopApplyWithdraw`,
    method: 'post',
    data
  })
}
// 获取店铺资产信息
export function getShopAsset(id) {
  return request({
    url: `/sys/shopAsset/getShopAsset?shopId=${id}`,
    method: 'get',
  })
}
