import request from '@/utils/request'
// 充值记录分页
export function getRechargePage(data) {
  return request({
    url: `/order/recharge/getRechargePage`,
    method: 'post',
    data
  })
}
// 提现记录分页
export function getShopWithdrawList(data) {
  return request({
    url: `/order/mall/withdraw/getShopWithdrawList`,
    method: 'post',
    data
  })
}
// 提现详情
export function getShopWithdrawInfo(id) {
  return request({
    url: `/order/mall/withdraw/getShopWithdrawInfo?id=${id}`,
    method: 'get',
  })
}
// 不可用明细列表
export function getShopAssetFreezeRecordList(data) {
  return request({
    url: `/sys/shopAsset/getShopAssetFreezeRecordList`,
    method: 'post',
    data
  })
}
// 获取店铺全部交易记录
export function getShopAssetRecord(data) {
  return request({
    url: `/sys/shopAsset/getShopAssetRecord`,
    method: 'post',
    data
  })
}