<template>
  <div>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="充值时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="操作人：">
            <el-input style="width: 203px" v-model="searchList.operateUser" clearable @input="change($event)" placeholder="姓名/手机号"></el-input>
          </el-form-item>
          <el-form-item label="充值状态：">
            <el-select v-model="searchList.payStatus" clearable>
              <el-option label="充值成功" value="2"></el-option>
              <el-option label="充值中" value="1"></el-option>
              <el-option label="充值失败" value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="支付方式：">
            <el-select v-model="searchList.payType" clearable>
              <el-option label="微信" value="1"></el-option>
              <el-option label="支付宝" value="2"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-start" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>充值记录</span>
        </div>
        <div class="wordInfo flex-row-start">
          <div class="word warningFont">共充值{{rechargeCount}}次</div>
          <div class="word warningFont">共充值：{{rechargeMoney}}元</div>
          <div class="word warningFont">店铺可用余额：{{shopAsset}}元</div>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table
          ref="multipleTable"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column label="充值时间" width="200">
            <template slot-scope="scope">
              {{ scope.row.createTime }}
            </template>
          </el-table-column>
          <el-table-column prop="payStatusName" label="名称" width="200"></el-table-column>
          <el-table-column label="收支类型" width="200">
            <template slot-scope="scope"><span :data-id="scope.row.id">收入</span></template>
          </el-table-column>
          <el-table-column label="支付流水号" width="200" prop="rechargeNo"></el-table-column>
          <el-table-column label="金额" width="200" prop="payMoney"></el-table-column>
          <el-table-column label="充值方式" width="200" prop="payTypeName"></el-table-column>
          <el-table-column label="操作人" width="300" prop="operateUser"></el-table-column>
          <el-table-column label="状态" width="200" prop="payStatusName"></el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getRechargePage } from '@/api/assetsManage/account'
import { getShopAsset } from '@/api/assetsManage/verifyFund'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了充值记录')
        this.searchList.pageNum = 1;
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        time:[],
        pageNum:1,
        pageSize:5,
        payType:'',
        payStatus:''
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      rechargeCount:'',
      rechargeMoney:'',
      shopAsset:'',
      list:[],
      listTotal:0,
      dataList:{}
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
    this.getMoney()
  },
  methods:{
    getMoney(){
      getShopAsset(this.shop.shopId).then(res=>{
        console.log('默认数据',res)
        if(res.code == 200){
          this.shopAsset = res.data.shopAsset;
        }
      })
    },
    getData(){
      console.log('请求的数据',this.searchList)
      this.searchList.startDate = this.searchList.time[0] || ''
      this.searchList.endDate = this.searchList.time[1] || ''
      getRechargePage(this.searchList).then(res=>{
        console.log('充值记录数据列表',res)
        if(res.code == 200){
          this.rechargeMoney = res.data.rechargeMoney;
          this.rechargeCount = res.data.rechargeCount;
          this.list = res.data.page.list
          this.listTotal = res.data.page.total
        }
      })
    },
    reset(){
      this.searchList.time = []
      this.searchList.operateUser = ''
      this.searchList.payStatus = ''
      this.searchList.payType = ''
      this.getData()
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .wordInfo{
    margin-left: 80px;
    .word{
      margin-right: 40px;
      font-size: 16px;
      font-weight: bold;
    }
  }
</style>